      title: 'Collection and Use of Non-Personal Information',
      icon: <InfoOutlinedIcon sx={{ color: '#0288d1' }} />,
      points: [
        'We may collect non-personal information about Users whenever they use and interact with the Services. Non-personal information may include the browser name, the type of computer, and technical information about means used by the User to connect to our Services, such as information about the operating system and the internet service providers utilized, and other similar information.',
        'This information is aggregated and used to help us provide more useful information to the User and to understand which parts of the Services, its products, and services are of most interest.'
      ],
